import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { PageLayout } from '../components/layout';
import { Card, CardContent, CardHeader } from '../components/ui/card';
import { Button } from '../components/ui/button';
import { Badge } from '../components/ui/badge';
import { useToast } from '../components/ui/toast';
import { useAuth } from '../contexts/AuthContext';
import apiService from '../services/api';
import config from '../config/config';
import { cn } from '../lib/utils';
import {
  ArrowLeft,
  BookOpen,
  CheckCircle,
  ChevronDown,
  ChevronUp,
  Clock,
  FileText,
  Layers,
  Loader2,
  PlayCircle,
  Shield,
  Sparkles,
} from 'lucide-react';

const CourseDetailPage = () => {
  const { identifier } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const { addToast } = useToast();

  const [course, setCourse] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [accessRestriction, setAccessRestriction] = useState(null);
  const [lessonProgress, setLessonProgress] = useState({});
  const [progressPercentage, setProgressPercentage] = useState(0);
  const [openModules, setOpenModules] = useState({});
  const [savingLesson, setSavingLesson] = useState(0);
  const [enrolling, setEnrolling] = useState(false);

  const courseIdentifier = identifier || course?.slug || course?.id;

  const toggleModule = (moduleId) => {
    setOpenModules((prev) => ({
      ...prev,
      [moduleId]: !prev[moduleId],
    }));
  };

  const buildLessonMap = useCallback((lessons = []) => {
    const map = {};
    lessons.forEach((lesson) => {
      map[lesson.lesson_id] = lesson;
    });
    return map;
  }, []);

  const loadProgress = useCallback(async () => {
    if (!courseIdentifier) return;
    try {
      const response = await apiService.getCourseProgress(courseIdentifier);
      if (response?.success && response.progress) {
        setLessonProgress(buildLessonMap(response.progress.lessons || []));
        setProgressPercentage(response.progress.progress || 0);
        setCourse((prev) =>
          prev
            ? {
                ...prev,
                enrollment: prev.enrollment
                  ? { ...prev.enrollment, progresso: response.progress.progress }
                  : prev.enrollment,
              }
            : prev
        );
      }
    } catch (err) {
      console.error('Erro ao carregar progresso do curso:', err);
    }
  }, [courseIdentifier, buildLessonMap]);

  const fetchCourse = useCallback(async () => {
    if (!identifier) return;
    setLoading(true);
    setError('');
    setAccessRestriction(null);

    try {
      const response = await apiService.getCourse(identifier);
      if (response?.success && response.course) {
        const courseData = response.course;
        setCourse(courseData);
        const initialModules = {};
        (courseData.modules || []).forEach((module) => {
          initialModules[module.id] = true;
        });
        setOpenModules(initialModules);

        if (courseData.enrollment) {
          await loadProgress();
        } else {
          setLessonProgress({});
          setProgressPercentage(0);
        }
      } else {
        setError(response?.message || 'Curso não encontrado.');
      }
    } catch (err) {
      console.error('Erro ao carregar curso:', err);
      if (err?.status === 403) {
        setAccessRestriction({
          message: err?.data?.message || 'Seu plano atual não permite acesso a este curso.',
          subscription: err?.data?.subscription || null,
        });
      } else if (err?.status === 404) {
        setError('Curso não encontrado ou indisponível no momento.');
      } else {
        setError(err?.message || 'Erro ao carregar curso.');
      }
    } finally {
      setLoading(false);
    }
  }, [identifier, loadProgress]);

  useEffect(() => {
    fetchCourse();
  }, [fetchCourse]);

  const handleEnroll = async () => {
    if (!courseIdentifier) return;
    setEnrolling(true);
    try {
      const response = await apiService.enrollCourse(courseIdentifier);
      if (response?.success) {
        addToast('Inscrição realizada com sucesso!', 'success');
        await fetchCourse();
      } else {
        addToast(response?.message || 'Não foi possível concluir a inscrição.', 'error');
      }
    } catch (err) {
      console.error('Erro ao inscrever no curso:', err);
      if (err?.status === 403) {
        setAccessRestriction({
          message: err?.data?.message || 'Seu plano atual não permite acesso a este curso.',
          subscription: err?.data?.subscription || null,
        });
      }
      addToast(err?.message || 'Erro ao realizar inscrição.', 'error');
    } finally {
      setEnrolling(false);
    }
  };

  const handleToggleLesson = async (lesson) => {
    if (!course?.enrollment) {
      addToast('Inscreva-se no curso para marcar seu progresso.', 'error');
      return;
    }

    setSavingLesson(lesson.id);
    try {
      const currentStatus = lessonProgress[lesson.id]?.watched || false;
      await apiService.updateLessonProgress(courseIdentifier, lesson.id, {
        watched: !currentStatus,
      });
      await loadProgress();
      addToast(!currentStatus ? 'Lição marcada como concluída!' : 'Progresso revertido.', 'success');
    } catch (err) {
      console.error('Erro ao atualizar progresso da lição:', err);
      addToast(err?.message || 'Não foi possível atualizar o progresso.', 'error');
    } finally {
      setSavingLesson(0);
    }
  };

  const heroBackground = useMemo(() => {
    if (!course?.imagem_capa) return null;
    return {
      backgroundImage: `linear-gradient(rgba(15,23,42,0.68), rgba(15,23,42,0.72)), url(${config.getPublicUrl(course.imagem_capa)})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    };
  }, [course?.imagem_capa]);

  const renderRestriction = () => (
    <div className="rounded-2xl border border-amber-200 bg-amber-50 p-8 text-center">
      <Sparkles className="mx-auto h-10 w-10 text-amber-500" />
      <h2 className="mt-4 text-xl font-semibold text-gray-900">Conteúdo exclusivo para assinantes</h2>
      <p className="mt-2 text-gray-600">
        {accessRestriction?.message || 'Faça upgrade do seu plano para desbloquear os cursos completos da EmpowerUp.'}
      </p>
      <div className="mt-6 flex justify-center gap-3">
        <Button variant="outline" onClick={() => navigate(-1)}>
          Voltar
        </Button>
        <Button asChild className="bg-coral hover:bg-coral/90">
          <Link to="/planos">Conhecer planos</Link>
        </Button>
      </div>
    </div>
  );

  if (loading) {
    return (
      <PageLayout title="Carregando curso...">
        <div className="space-y-6">
          <div className="h-60 w-full animate-pulse rounded-2xl bg-gray-200" />
          <div className="grid gap-4 lg:grid-cols-3">
            <div className="space-y-4">
              <div className="h-10 animate-pulse rounded bg-gray-200" />
              <div className="h-10 animate-pulse rounded bg-gray-200" />
              <div className="h-10 animate-pulse rounded bg-gray-200" />
            </div>
            <div className="lg:col-span-2 space-y-4">
              {Array.from({ length: 4 }).map((_, index) => (
                <div key={index} className="h-28 animate-pulse rounded-lg bg-gray-200" />
              ))}
            </div>
          </div>
        </div>
      </PageLayout>
    );
  }

  if (error) {
    return (
      <PageLayout title="Curso indisponível">
        <div className="rounded-2xl border border-red-200 bg-red-50 p-8 text-center">
          <Shield className="mx-auto h-10 w-10 text-red-500" />
          <h2 className="mt-4 text-xl font-semibold text-gray-900">Ops! Algo deu errado.</h2>
          <p className="mt-2 text-gray-600">{error}</p>
          <div className="mt-6">
            <Button onClick={() => navigate('/cursos')}>Voltar para cursos</Button>
          </div>
        </div>
      </PageLayout>
    );
  }

  if (accessRestriction) {
    return (
      <PageLayout title="Acesso restrito">
        {renderRestriction()}
      </PageLayout>
    );
  }

  if (!course) {
    return null;
  }

  const enrollment = course.enrollment;
  const totalModules = course.modules?.length || 0;
  const totalLessons = course.modules?.reduce((count, module) => count + (module.lessons?.length || 0), 0) || 0;

  return (
    <div className="bg-gray-50">
      <div className="-mt-8">
        <div className="relative overflow-hidden rounded-none md:rounded-b-[40px]" style={heroBackground || undefined}>
          <div className={cn('relative z-10 px-4 py-12 sm:px-8 lg:px-16', heroBackground ? 'text-white' : 'text-gray-900 bg-gradient-to-r from-white to-emerald-50')}>
            <div className="flex items-center gap-3 text-sm font-medium uppercase tracking-wide opacity-80">
              <Button variant="ghost" size="sm" onClick={() => navigate(-1)} className={cn('gap-2', heroBackground ? 'text-white hover:text-white/80' : 'text-gray-700')}>
                <ArrowLeft className="h-4 w-4" /> Voltar
              </Button>
              <span>Curso • EmpowerUp Academy</span>
            </div>

            <div className="mt-6 flex flex-wrap items-center gap-4">
              {course.categoria && <Badge variant="secondary">{course.categoria}</Badge>}
              {course.nivel && <Badge variant="outline">Nível {course.nivel}</Badge>}
              {course.destaque && (
                <Badge className="bg-amber-500/90 text-white">
                  <Sparkles className="mr-1 h-3.5 w-3.5" /> Destaque
                </Badge>
              )}
            </div>

            <h1 className="mt-4 max-w-3xl text-3xl font-bold leading-tight sm:text-4xl">
              {course.titulo}
            </h1>

            <p className="mt-4 max-w-2xl text-base sm:text-lg opacity-90">
              {course.descricao || 'Domine novas habilidades com aulas práticas e materiais exclusivos desenvolvidos para empreendedoras.'}
            </p>

            <div className="mt-8 flex flex-wrap gap-6 text-sm">
              <div className="flex items-center gap-2">
                <Layers className="h-5 w-5" /> {totalModules} módulo{totalModules === 1 ? '' : 's'}
              </div>
              <div className="flex items-center gap-2">
                <BookOpen className="h-5 w-5" /> {totalLessons} aula{totalLessons === 1 ? '' : 's'}
              </div>
              {course.duracao_estimado && (
                <div className="flex items-center gap-2">
                  <Clock className="h-5 w-5" />
                  {course.duracao_estimado} min estimados
                </div>
              )}
            </div>

            <div className="mt-8 flex flex-wrap items-center gap-4">
              {enrollment ? (
                <Badge variant="secondary" className="bg-emerald-500/90 text-white">
                  Você está inscrita
                </Badge>
              ) : (
                <Button
                  size="lg"
                  className="bg-coral hover:bg-coral/90"
                  disabled={enrolling}
                  onClick={handleEnroll}
                >
                  {enrolling ? <Loader2 className="h-5 w-5 animate-spin" /> : 'Inscrever-se agora'}
                </Button>
              )}

              {enrollment && (
                <div className="min-w-[220px] rounded-full bg-white/20 px-4 py-2 text-sm">
                  Progresso: {Math.round(progressPercentage)}%
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      <PageLayout className="bg-transparent" breadcrumbs={[{ label: 'Início', href: '/' }, { label: 'Cursos', href: '/cursos' }, { label: course.titulo }]}>
        <div className="grid gap-8 lg:grid-cols-[2fr,1fr]">
          <div className="space-y-6">
            {course.modules?.map((module, index) => {
              const isOpen = openModules[module.id] ?? true;
              const completedLessons = module.lessons?.filter((lesson) => lessonProgress[lesson.id]?.watched)?.length || 0;
              const totalModuleLessons = module.lessons?.length || 0;

              return (
                <Card key={module.id} className="border border-gray-200 shadow-sm">
                  <CardHeader
                    className={cn(
                      'flex cursor-pointer items-center justify-between gap-3',
                      isOpen ? 'border-b border-gray-100 pb-3' : ''
                    )}
                    onClick={() => toggleModule(module.id)}
                  >
                    <div>
                      <div className="text-xs font-semibold uppercase tracking-wide text-coral">Módulo {index + 1}</div>
                      <h2 className="text-lg font-semibold text-gray-900">{module.titulo}</h2>
                      {module.descricao && (
                        <p className="mt-1 text-sm text-gray-600 line-clamp-2">{module.descricao}</p>
                      )}
                    </div>
                    <div className="flex items-center gap-3 text-sm text-gray-500">
                      <span>
                        {completedLessons}/{totalModuleLessons} aulas concluídas
                      </span>
                      <Button variant="ghost" size="icon" className="h-8 w-8" aria-label="Alternar módulo">
                        {isOpen ? <ChevronUp className="h-5 w-5" /> : <ChevronDown className="h-5 w-5" />}
                      </Button>
                    </div>
                  </CardHeader>
                  {isOpen && (
                    <CardContent className="space-y-4">
                      {module.lessons?.length ? (
                        module.lessons.map((lesson) => {
                          const progress = lessonProgress[lesson.id];
                          const watched = Boolean(progress?.watched);
                          return (
                            <div
                              key={lesson.id}
                              className={cn(
                                'rounded-lg border p-4 transition-colors',
                                watched
                                  ? 'border-emerald-200 bg-emerald-50'
                                  : 'border-gray-200 bg-white hover:border-coral/40'
                              )}
                            >
                              <div className="flex flex-col gap-3 sm:flex-row sm:items-start sm:justify-between">
                                <div className="flex-1">
                                  <div className="flex flex-wrap items-center gap-2 text-sm font-medium text-gray-900">
                                    {watched ? (
                                      <CheckCircle className="h-4 w-4 text-emerald-500" />
                                    ) : (
                                      <PlayCircle className="h-4 w-4 text-coral" />
                                    )}
                                    <span>{lesson.titulo}</span>
                                    {lesson.tipo && (
                                      <Badge variant="outline" className="text-xs uppercase">
                                        {lesson.tipo}
                                      </Badge>
                                    )}
                                  </div>
                                  {lesson.descricao && (
                                    <p className="mt-2 text-sm text-gray-600">{lesson.descricao}</p>
                                  )}
                                  <div className="mt-3 flex flex-wrap items-center gap-4 text-xs text-gray-500">
                                    {lesson.duracao_min && (
                                      <span className="inline-flex items-center gap-1">
                                        <Clock className="h-3.5 w-3.5" />
                                        {lesson.duracao_min} min
                                      </span>
                                    )}
                                    {lesson.conteudo_url && (
                                      <a
                                        className="inline-flex items-center gap-1 text-coral hover:text-coral/80"
                                        href={lesson.conteudo_url}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                      >
                                        <FileText className="h-3.5 w-3.5" />
                                        Abrir conteúdo
                                      </a>
                                    )}
                                    {progress?.watched_at && (
                                      <span>Concluída em {new Date(progress.watched_at).toLocaleDateString('pt-BR')}</span>
                                    )}
                                  </div>
                                </div>
                                <div className="flex items-center gap-2">
                                  <Button
                                    size="sm"
                                    variant={watched ? 'outline' : 'default'}
                                    className={cn(watched ? 'border-emerald-400 text-emerald-700' : 'bg-coral hover:bg-coral/90')}
                                    disabled={savingLesson === lesson.id}
                                    onClick={() => handleToggleLesson(lesson)}
                                  >
                                    {savingLesson === lesson.id ? (
                                      <Loader2 className="h-4 w-4 animate-spin" />
                                    ) : watched ? (
                                      'Marcar como pendente'
                                    ) : (
                                      'Concluir lição'
                                    )}
                                  </Button>
                                </div>
                              </div>
                            </div>
                          );
                        })
                      ) : (
                        <div className="rounded-lg border border-dashed border-gray-200 bg-gray-50 p-6 text-center text-sm text-gray-500">
                          Nenhuma lição cadastrada para este módulo ainda.
                        </div>
                      )}
                    </CardContent>
                  )}
                </Card>
              );
            })}
          </div>

          <aside className="space-y-6">
            <Card className="border border-gray-200 shadow-sm">
              <CardHeader>
                <h3 className="text-lg font-semibold text-gray-900">Seu progresso</h3>
                <p className="text-sm text-gray-600">
                  Acompanhe seu avanço e conclua todas as aulas para desbloquear certificados.
                </p>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <div className="flex items-center justify-between text-sm text-gray-600">
                    <span>{Math.round(progressPercentage)}% concluído</span>
                    <span>
                      {Object.values(lessonProgress).filter((item) => item.watched).length}/{totalLessons} aulas
                    </span>
                  </div>
                  <div className="mt-2 h-3 w-full overflow-hidden rounded-full bg-gray-200">
                    <div
                      className="h-full rounded-full bg-coral transition-all"
                      style={{ width: `${Math.min(100, Math.max(0, progressPercentage))}%` }}
                    />
                  </div>
                </div>

                {enrollment ? (
                  <div className="rounded-lg border border-emerald-200 bg-emerald-50 p-4 text-sm text-emerald-700">
                    Você iniciou este curso em{' '}
                    {enrollment.iniciado_em ? new Date(enrollment.iniciado_em).toLocaleDateString('pt-BR') : 'recentemente'}.
                  </div>
                ) : (
                  <div className="rounded-lg border border-coral/40 bg-coral/5 p-4 text-sm text-coral-700">
                    Faça sua inscrição para liberar o conteúdo completo e registrar seu progresso.
                  </div>
                )}
              </CardContent>
            </Card>

            {course.resources?.length ? (
              <Card className="border border-gray-200 shadow-sm">
                <CardHeader>
                  <h3 className="text-lg font-semibold text-gray-900">Materiais de apoio</h3>
                  <p className="text-sm text-gray-600">Links e arquivos complementares para ampliar seu aprendizado.</p>
                </CardHeader>
                <CardContent className="space-y-3">
                  {course.resources.map((resource) => (
                    <a
                      key={resource.id}
                      href={resource.url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="flex items-center justify-between rounded-lg border border-gray-200 p-3 text-sm text-gray-700 transition-colors hover:border-coral/60 hover:bg-coral/5"
                    >
                      <span className="font-medium">{resource.titulo}</span>
                      <FileText className="h-4 w-4 text-coral" />
                    </a>
                  ))}
                </CardContent>
              </Card>
            ) : null}

            <Card className="border border-gray-200 shadow-sm">
              <CardHeader>
                <h3 className="text-lg font-semibold text-gray-900">Dica EmpowerUp</h3>
              </CardHeader>
              <CardContent className="space-y-3 text-sm text-gray-600">
                <p>
                  Aproveite os fóruns e grupos da comunidade para compartilhar dúvidas sobre este curso. O aprendizado
                  em rede acelera seu desenvolvimento!
                </p>
                <Button asChild variant="outline" size="sm" className="w-full">
                  <Link to={enrollment ? '/comunidade?tab=posts' : '/planos'}>
                    {enrollment ? 'Ir para a comunidade' : 'Conhecer planos' }
                  </Link>
                </Button>
              </CardContent>
            </Card>
          </aside>
        </div>
      </PageLayout>
    </div>
  );
};

export default CourseDetailPage;
